import request from "@/utils/request";

//create business
export function createBusiness(token, data) {
  return request({
    headers: {
      "Content-Type": "application/json",
      Accept: "application/json",
      Authorization: `Bearer ${token}`
    },
    url: "/api/v1/businesses",
    method: "post",
    data
  });
}

//update business
export function updateBusiness(token, id, data) {
  return request({
    headers: {
      "Content-Type": "application/json",
      Accept: "application/json",
      Authorization: `Bearer ${token}`
    },
    url: `/api/v1/businesses/${id}`,
    method: "post",
    data
  });
}

//get user businesses
export function getUserBusinesses(token, id) {
  return request({
    headers: {
      "Content-Type": "application/json",
      Accept: "application/json",
      Authorization: `Bearer ${token}`
    },
    url: `/api/v1/users/${id}/businesses`,
    method: "get"
  });
}



//get a business
export function getBusiness(token, id) {
  return request({
    headers: {
      "Content-Type": "application/json",
      Accept: "application/json",
      Authorization: `Bearer ${token}`
    },
    url: `/api/v1/businesses/${id}`,
    method: "get"
  });
}

// get QR code
export function getBusinessQR(businessId) {
  return request({
    url: `https://api.qrserver.com/v1/create-qr-code/?size=200x200&data=${businessId}`,
    method: "get"
  });
}

