<template>
  <div class="app-container">
    <div class="flex justify-center p-5 relative">
      <router-link to="/login" class="absolute top-0 left-0 p-5 flex items-center h-full">
        <img src="@/assets/icons/close.svg" alt="icon" class="qr-icon-close" />
      </router-link>
      <div class="flex items-center h-full">
        <span class="font-semibold text-2xl tracking-wide">Sign Up</span>
      </div>
    </div>

    <div class="min-h-screen p-5 pt-0 mt-16">
      <form autocomplete="off" @submit.prevent="handleSignUp">
        <div class="mb-5">
          <label for="name" class="hidden">Name</label>
          <input
            v-model="registerForm.name"
            type="text"
            placeholder="Name"
            id="name"
            required
            autocomplete="off"
            class="w-full p-3 px-5 border-2 border-opacity-50 border-pink-700 rounded-lg h-16 text-xl placeholder-black placeholder-opacity-50 focus:border-teal-500 focus:outline-none tracking-wide"
          />
        </div>
        <div class="mb-5">
          <label for="email" class="hidden">Email</label>
          <input
            v-model="registerForm.email"
            type="email"
            placeholder="Email"
            id="email"
            required
            autocomplete="off"
            class="w-full p-3 px-5 border-2 border-opacity-50 border-pink-700 rounded-lg h-16 text-xl placeholder-black placeholder-opacity-50 focus:border-teal-500 focus:outline-none tracking-wide"
          />
        </div>
        <div class="mb-5">
          <label for="phone" class="hidden">Phone</label>
          <input
            v-model="registerForm.phone"
            type="tel"
            placeholder="Phone"
            id="phone"
            required
            autocomplete="off"
            class="w-full p-3 px-5 border-2 border-opacity-50 border-pink-500 rounded-lg h-16 text-xl placeholder-black placeholder-opacity-50 focus:border-teal-500 focus:outline-none tracking-wide"
          />
        </div>

        <div class="mb-5">
          <label for="password" class="hidden">Password</label>
          <input
            v-model="registerForm.password"
            type="password"
            placeholder="Password"
            id="password"
            required
            autocomplete="off"
            class="w-full p-3 px-5 border-2 border-opacity-50 border-pink-700 rounded-lg h-16 text-xl placeholder-black placeholder-opacity-50 focus:border-teal-500 focus:outline-none tracking-wide"
          />
        </div>
        <div class="mb-6">
          <label for="password_confirmation" class="hidden">Confirm Password</label>
          <input
            v-model="registerForm.password_confirmation"
            type="password"
            placeholder="Confirm Password"
            id="password_confirmation"
            required
            autocomplete="off"
            class="w-full p-3 px-5 border-2 border-opacity-50 border-pink-700 rounded-lg h-16 text-xl placeholder-black placeholder-opacity-50 focus:border-teal-500 focus:outline-none tracking-wide"
          />
        </div>

        <!-- User type -->
        <div v-show="false"
          class="mb-6 flex justify-between p-3 px-5 border-2 border-opacity-50 border-pink-700 rounded-lg"
        >
          <label for="role-customer" class="qr-checkbox flex items-center">
            <div class="bg-white shadow w-6 h-6 p-1 flex justify-center items-center mr-2">
              <input
                type="radio"
                v-model="registerForm.role"
                class="hidden"
                value="customer"
                checked
                id="role-customer"
              />
              <svg class="hidden w-4 h-4 text-pink-600 pointer-events-none" viewBox="0 0 172 172">
                <g
                  fill="none"
                  stroke-width="none"
                  stroke-miterlimit="10"
                  font-family="none"
                  font-weight="none"
                  font-size="none"
                  text-anchor="none"
                  style="mix-blend-mode:normal"
                >
                  <path d="M0 172V0h172v172z" />
                  <path
                    d="M145.433 37.933L64.5 118.8658 33.7337 88.0996l-10.134 10.1341L64.5 139.1341l91.067-91.067z"
                    fill="currentColor"
                    stroke-width="1"
                  />
                </g>
              </svg>
            </div>
            <span class="select-none">Customer</span>
          </label>

          <label for="role-merchant" class="qr-checkbox flex items-center">
            <div class="bg-white shadow w-6 h-6 p-1 flex justify-center items-center mr-2">
              <input
                type="radio"
                v-model="registerForm.role"
                class="hidden"
                value="merchant"
                id="role-merchant"
              />
              <svg class="hidden w-4 h-4 text-pink-600 pointer-events-none" viewBox="0 0 172 172">
                <g
                  fill="none"
                  stroke-width="none"
                  stroke-miterlimit="10"
                  font-family="none"
                  font-weight="none"
                  font-size="none"
                  text-anchor="none"
                  style="mix-blend-mode:normal"
                >
                  <path d="M0 172V0h172v172z" />
                  <path
                    d="M145.433 37.933L64.5 118.8658 33.7337 88.0996l-10.134 10.1341L64.5 139.1341l91.067-91.067z"
                    fill="currentColor"
                    stroke-width="1"
                  />
                </g>
              </svg>
            </div>
            <span class="select-none">Merchant</span>
          </label>
        </div>

        <div class="mb-6 flex justify-center">
          <label for="accept_terms" class="qr-checkbox flex items-center">
            <div class="bg-white border border-red-600 shadow w-6 h-6 p-1 flex justify-center items-center mr-2">
              <input
                type="checkbox"
                v-model="accept_terms"
                class="hidden"
                checked
                id="accept_terms"
              />
              <svg class="hidden w-4 h-4 text-pink-600 pointer-events-none" viewBox="0 0 172 172">
                <g
                  fill="none"
                  stroke-width="none"
                  stroke-miterlimit="10"
                  font-family="none"
                  font-weight="none"
                  font-size="none"
                  text-anchor="none"
                  style="mix-blend-mode:normal"
                >
                  <path d="M0 172V0h172v172z" />
                  <path
                    d="M145.433 37.933L64.5 118.8658 33.7337 88.0996l-10.134 10.1341L64.5 139.1341l91.067-91.067z"
                    fill="currentColor"
                    stroke-width="1"
                  />
                </g>
              </svg>
            </div>
            <span class="select-none">Accept Terms &amp; Conditions</span>
          </label>
        </div>

        <Errors :errors="validationErrors" v-if="validationErrors" class="mb-6" />

        <div class="mb-8 flex justify-center">
          <button
            :disabled="!accept_terms"
            class="w -full p-3 px-5 border border-pink-700 rounded-md h-16 text-xl placeholder-black focus:border-teal-500 focus:outline-none tracking-wide qr-btn text-white w-full"
          >Sign Up</button>
        </div>
      </form>

      <div class="mb-6 flex justify-center">
        <router-link to="/login">Got an account?</router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { signup } from "@/api/user";
import Errors from "@/components/Errors";

export default {
  name: "Register",
  components: { Errors },
  data() {
    return {
      accept_terms: false,
      registerForm: {
        name: null,
        phone: null,
        email: null,
        password: null,
        password_confirmation: null,
        role: "customer"
      },
      validationErrors: null
    };
  },
  methods: {
    validateForm() {
      let self = this;
      if (
        self.registerForm.name == null ||
        self.registerForm.phone == null ||
        self.registerForm.email == null ||
        self.registerForm.password == null ||
        self.registerForm.password_confirmation == null
      ) {
        self.$msg.info({
          message: "Some fields are missing!",
          width: "80%",
          position: "bottom-center"
        });
        return false;
      }

      if (
        self.registerForm.password !== self.registerForm.password_confirmation
      ) {
        self.$msg.info({
          message: "Passwords do not match!",
          width: "80%",
          position: "bottom-center"
        });
        return false;
      }

      return true;
    },
    handleSignUp() {
      let self = this;
      if (self.validateForm) {
        let loading = self.$loading.show({
          color: "purple",
          canCancel: false
        });

        signup(self.registerForm)
          .then(
            response => {
              loading.hide();
              console.log(response);
              localStorage.setItem('_username', self.registerForm.email)
              localStorage.setItem('_password', self.registerForm.password_confirmation)
              self.handleLogin(
                self.registerForm.email,
                self.registerForm.password_confirmation
              );
            },
            error => {
              loading.hide();
              this.validationErrors = error.data;
            }
          )
          .catch(err => {
            loading.hide();
            console.log(err);
            self.$msg.error({
              message: `Network Error!`,
              width: "80%",
              position: "center"
            });
          });
      }
    },
    handleLogin(email, password) {
      let self = this;

      if (email == null || password == null) {
        self.$msg.info({
          message: "Email or Password is missing!",
          width: "80%",
          position: "bottom-center"
        });
        return;
      }

      let loginForm = {
        email: email,
        password: password,
        remember_me: false
      };

      let loading = self.$loading.show({
        message: "Please wait!",
        color: "purple",
        canCancel: false
      });

      this.$store
        .dispatch("user/login", loginForm)
        .then(() => {
          loading.hide();
          this.$router.push({ path: this.redirect || "/" });
        })
        .catch(error => {
          loading.hide();
          self.$msg.error({
            message: error.data.message,
            width: "80%",
            position: "top-center"
          });
          console.log(`Error ${error}`);
        });
    }
  }
};
</script>

<style lang="scss" scoped>
.qr-icon-close {
  width: 25px;
}
.qr-checkbox input:checked + svg {
  display: block !important;
}
.qr-btn {
  background: rgb(156, 56, 216);
  background: linear-gradient(
    90deg,
    rgba(156, 56, 216, 1) 0%,
    rgba(252, 110, 152, 1) 100%
  );
}
button:disabled {
  opacity: 0.6;
}
</style>
