import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

import Register from "../views/register/index.vue";
import Login from "../views/login/index.vue";
import RecoverPassword_1 from "../views/forgotpassword/step-1.vue";
import RecoverPassword_2 from "../views/forgotpassword/step-2.vue";
import qr from "../views/business/qr.vue";
import payment from "../views/payment/index.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/register",
    name: "Register",
    component: Register
  },
  {
    path: "/login",
    name: "Login",
    component: Login
  },
  {
    path: "/recoverpassword/s1",
    name: "step-1",
    component: RecoverPassword_1
  },
  {
    path: "/recoverpassword/s2",
    name: "step-2",
    component: RecoverPassword_2
  },
  {
    path: "/qr/:id/:name",
    name: "qr",
    component: qr
  },
  {
    path: "/payment/:id",
    name: "payment",
    component: payment
  }
];

const router = new VueRouter({
  routes
});

export default router;
