<template>
  <div v-if="validationErrors">
    <ul class="alert alert-danger">
      <li v-for="(value, key) in validationErrors" :key="key">{{value}}</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Errors",
  data() {
    return {};
  },
  props: ["errors"],
  computed: {
    validationErrors() {
      let errors = Object.values(this.errors);
      errors = errors.flat();
      return errors;
    }
  }
};
</script>

<style scoped>
ul {
  padding: 1rem;
  background-color: #eee;
}
li {
  text-align: center;
  color: red;
  display: block;
  margin-bottom: 0.5rem;
}
</style>
