<template>
  <div class="app-container">
    <div class="flex justify-center p-5 relative">
      <router-link to="/register" class="absolute top-0 left-0 p-5 flex items-center h-full">
        <img src="@/assets/icons/close.svg" alt="icon" class="qr-icon-close" />
      </router-link>
      <div class="flex items-center h-full">
        <span class="font-semibold text-2xl tracking-wide">Sign In</span>
      </div>
    </div>

    <div class="flex justify-center mt-8 mb-8 hidden">
      <img src="@/assets/icons/user.svg" alt="icon" class="qr-icon-user" />
    </div>

    <div class="qr-min-h p-5 relative mt-8">
      <form autocomplete="off" @submit.prevent="handleLogin">
        <div class="mb-5">
          <label for="email" class="hidden">Email</label>
          <input
            v-model="loginForm.email"
            type="email"
            placeholder="Email"
            id="login_email"
            required
            autocomplete="off"
            class="w-full p-3 px-5 border-2 border-opacity-50 border-pink-700 rounded-lg h-16 text-xl placeholder-black placeholder-opacity-50 focus:border-teal-500 focus:outline-none tracking-wide"
          />
        </div>
        <div class="mb-5">
          <label for="password" class="hidden">Password</label>
          <input
            v-model="loginForm.password"
            type="password"
            placeholder="Password"
            id="login_password"
            required
            autocomplete="off"
            class="w-full p-3 px-5 border-2 border-opacity-50 border-pink-700 rounded-lg h-16 text-xl placeholder-black placeholder-opacity-50 focus:border-teal-500 focus:outline-none tracking-wide"
          />
        </div>

        <div class="mb-6 flex justify-center">
          <label for="remember_me" class="qr-checkbox flex items-center">
            <div class="bg-white shadow w-6 h-6 p-1 flex justify-center items-center mr-2">
              <input type="checkbox" class="hidden" checked id="remember_me" />
              <svg class="hidden w-4 h-4 text-pink-600 pointer-events-none" viewBox="0 0 172 172">
                <g
                  fill="none"
                  stroke-width="none"
                  stroke-miterlimit="10"
                  font-family="none"
                  font-weight="none"
                  font-size="none"
                  text-anchor="none"
                  style="mix-blend-mode:normal"
                >
                  <path d="M0 172V0h172v172z" />
                  <path
                    d="M145.433 37.933L64.5 118.8658 33.7337 88.0996l-10.134 10.1341L64.5 139.1341l91.067-91.067z"
                    fill="currentColor"
                    stroke-width="1"
                  />
                </g>
              </svg>
            </div>
            <span class="select-none">Don't ask me again</span>
          </label>
        </div>

        <div class="mb-8 flex justify-center">
          <button
            class="w-full p-3 px-5 border border-pink-700 rounded-md h-16 text-xl placeholder-black focus:border-teal-500 focus:outline-none tracking-wide qr-btn text-white"
          >Sign In</button>
        </div>
      </form>

      <div class="mb-6 flex justify-center">
        <router-link to="/recoverpassword/s1" class="hidden">Forgot Password?</router-link>
        <router-link to="/register" class="tracking-wide">Create a new account</router-link>
      </div>

      <div class="mb-4 flex justify-center left-0 bottom-0 p-2 pt-16 text-center w-full">
        <router-link
          to="/register"
          class="font-semibold text-lg tracking-wide hidden"
        >Create a new account</router-link>
        <router-link to="/register" class="font-semibold text-lg tracking-wide opacity-75">BETA 1.0</router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      loginForm: {
        email: null,
        password: null,
        remember_me: false
      },
      validationErrors: []
    };
  },
  created() {
    if(localStorage.getItem('_username')) {
      this.loginForm.email = localStorage.getItem('_username');
      this.loginForm.password = localStorage.getItem('_password')
    }
  },
  methods: {
    handleLogin() {
      let self = this;

      if (
        self.loginForm.email == null ||
        self.loginForm.email == "" ||
        self.loginForm.password == null ||
        self.loginForm.password == ""
      ) {
        self.$msg.info({
          message: "Email or Password is missing!",
          width: "80%",
          position: "bottom-center"
        });
        return;
      }

      let loading = self.$loading.show({
        color: "purple",
        canCancel: false
      });

      this.$store
        .dispatch("user/login", this.loginForm)
        .then(() => {
          loading.hide();
          this.$router.push({ path: this.redirect || "/" });
        })
        .catch(error => {
          loading.hide();
          self.$msg.error({
            message: error.data.message,
            width: "80%",
            position: "top-center"
          });
        })
        .catch(err => {
          console.log(err);
          self.$msg.error({
            message: "network Error!",
            width: "80%",
            position: "center"
          });
        });
    }
  }
};
</script>

<style lang="scss" scoped>
.qr-icon-close {
  width: 25px;
}
.qr-icon-user {
  width: 80px;
}
.qr-checkbox input:checked + svg {
  display: block !important;
}
.qr-btn {
  background: rgb(156, 56, 216);
  background: linear-gradient(
    90deg,
    rgba(156, 56, 216, 1) 0%,
    rgba(252, 110, 152, 1) 100%
  );
}
.qr-min-h {
  min-height: calc(100vh - 200px);
  @media (max-height: 500px) {
    min-height: calc(100vh - 50px);
  }
}
</style>
