import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";

import moment from "moment";

//loader
import Loading from "vue-loading-overlay";
import "vue-loading-overlay/dist/vue-loading.css";

//message
import Message from "vue-m-message";
import "vue-m-message/dist/index.css";

import "./assets/styles/index.css";

//permissions
import "./permissions";

Vue.use(Loading);
Vue.use(Message, { name: "msg" });

Vue.config.productionTip = false;

//Format date in MMM Do YYYY Format
Vue.filter("formatDate", function(date_) {
  return moment(date_).format("D/M/YYYY hh:mm");
});

Vue.filter("masker", function(number) {
  return number.substring(0, 4) + '###' + number.substring(number.length - 3);
});

new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount("#app");
