<template>
  <div :class="{'app-container relative':true, 'overflow-hidden':lockAppScroll}">
    <!-- User Customer Start -->
    <div
      v-if="currentUser.role == 'customer'"
      class="relative flex justify-center flex-col min-h-screen"
    >
      <div class="flex justify-center p-5 absolute top-0 left-0 w-full">
        <router-link to="/" class="mr-auto">
          <img src="@/assets/icons/arrow.svg" class="menu-icon" />
        </router-link>
        <span class="font-semibold text-xl text-white tracking-wide capitalize">{{businessName}}</span>
        <router-link to="#" class="ml-auto">
          <span>&nbsp;</span>
        </router-link>
      </div>

      <div class="img-wrapper p-5 text-center">
        <div class="inline-block relative p-4">
          <div class="qr-borders qr-top-left absolute top-0 left-0 absolute w-full h-full"></div>
          <div class="qr-borders qr-bottom-right bottom-0 right-0 absolute w-full h-full"></div>
          <img :src="qrImage" class="mx-auto qr-image text-white" alt="Loading QR Code.." />
        </div>
      </div>

      <div class="flex justify-center items-center absolute left-0 bottom-0 p-5 w-full">
        <button
          @click="openQrImageInBrowser"
          class="flex items-center w-full justify-center tracking-wide p-3 px-5 border border-pink-700 rounded-lg h-16 text-xl placeholder-black focus:outline-none tracking-wide qr-btn text-white"
        >DOWNLOAD</button>
      </div>
    </div>
    <!-- User Customer End-->
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { getToken } from "@/utils/auth";

export default {
  name: "Home",
  data() {
    return {
      token: null,
      currentUser: { role: null },
      loading: "",
      businessId: null,
      businessName: null,
      lockAppScroll: false,
      qrImage: null
    };
  },
  computed: {
    ...mapGetters(["name", "userId"])
  },
  watch: {
    qrImage: function() {
      console.log(this.qrImage);
      this.loading.hide();
    }
  },
  created() {
    this.currentUser.role = "customer";
    this.token = getToken();

    this.businessId = this.$route.params.id;
    this.businessName = this.$route.params.name;

    this.loading = this.$loading.show({
      color: "purple",
      canCancel: false
    });

    this.getQr();
  },
  mounted() {
    //let self = this;
  },
  methods: {
    async logout() {
      await this.$store.dispatch("user/logout");
      this.$router.push(`/login?redirect=${this.$route.fullPath}`);
    },
    getQr() {
      let self = this;

      setTimeout(function() {
        self.qrImage = `https://api.qrserver.com/v1/create-qr-code/?size=320x320&data=${self.businessId}`;
      }, 1000);
    },
    openQrImageInBrowser() {
      let self = this;
      var ref = window.open(self.qrImage, "_system", "location=yes");
      console.log(ref);
    }
  }
};
</script>

<style lang="scss" scoped>
.app-container {
  background: #3c3d4f;
}

.menu-icon {
  width: 30px;
}

.qr-image {
  max-height: 250px;
}

.qr-pink-bg {
  background: rgb(156, 56, 216);
  background: linear-gradient(
    90deg,
    rgba(156, 56, 216, 1) 0%,
    rgba(252, 110, 152, 1) 100%
  );
}

.qr-btn {
  background: rgb(156, 56, 216);
  background: linear-gradient(
    90deg,
    rgba(156, 56, 216, 1) 0%,
    rgba(252, 110, 152, 1) 100%
  );
}

.qr-gray-bg {
  background: #2e313a;
  background: linear-gradient(90deg, #2e313a 0%, #474956 100%);
}

.qr-borders {
  &::after,
  &::before {
    content: "";
    position: absolute;
    background: #999;
  }
}

.qr-top-left {
  &:before {
    top: 0;
    left: 0;
    width: 25%;
    height: 4px;
  }
  &:after {
    top: 0;
    left: 0;
    width: 4px;
    height: 25%;
  }
}

.qr-bottom-right {
  &:before {
    bottom: 0;
    right: 0;
    width: 25%;
    height: 4px;
  }
  &:after {
    bottom: 0;
    right: 0;
    width: 4px;
    height: 25%;
  }
}

.icon-after-span {
  width: 15px;
  margin-left: 0.5rem;
}

.list-icon {
  width: 35px;
}

.will-blur {
  transition: filter 0.5s ease-out;
}

.blur {
  pointer-events: none;
  filter: blur(3px) contrast(0.4);
}

/* Popup Animations */
.popup-enter,
.popup-leave-to {
  transform: translateY(-100%);
  opacity: 0;
}

.popup-enter-to,
.popup-leave {
  opacity: 1;
  transform: translateY(0);
}

.popup-enter-active,
.popup-leave-active {
  transition: all 0.3s ease-out;
  transform-origin: top center;
}
</style>

