<template>
  <div
    :class="{
      'app-container relative h-screen': true,
      'overflow-hidden': lockAppScroll,
    }"
  >
    <!-- User Customer Start -->
    <div
      v-if="currentUser.role == 'customer'"
      class="relative flex flex-col h-full"
    >
      <div class="flex justify-center p-5 absolute top-0 left-0 w-full z-50">
        <router-link to="#" class="mr-auto">
          <img src="@/assets/icons/menu-user.svg" class="menu-icon bg-blue-500 rounded-full" />
        </router-link>
        <span class="font-semibold text-xl text-white tracking-wide capitalize"
          >Scan</span
        >
        <button @click.prevent="logout" class="ml-auto">
          <img src="@/assets/icons/power.svg" class="menu-icon bg-green-500 rounded-full" />
        </button>
      </div>

        <qrcode-stream @decode="scanQR"></qrcode-stream>
      <div v-if="false" class="flex img-wrapper item-center justify-around min-h-full mt-24 p-5 pt-20 text-center">
       
        <div class="inline-block relative p-1" v-if="false">
          <div
            class="
              qr-borders qr-top-left
              absolute
              top-0
              left-0
              absolute
              w-full
              h-full
            "
          ></div>
          <div
            class="
              qr-borders qr-bottom-right
              bottom-0
              right-0
              absolute
              w-full
              h-full
            "
          ></div>
          <img src="@/assets/img/qr.png" class="mx-auto qr-image" />
        </div>
      </div>

      <!-- <div
        v-if="transactions.length >= 1"
        class="mt-2 p-5 flex justify-between items-center text-base"
      >
        <span class="text-white font--bold">
          Latest
          <span class="text-gray-400 font-normal">Transactions</span>
        </span>
        <button @click="customerTrx()" class="text-gray-400 text-white inline-flex items-center">
          <span>Reload</span>
          <img src="@/assets/icons/refresh.svg" class="icon-after-span" />
        </button>
      </div> -->

      <div class="p-5 pb-24" v-if="false">
        <!-- Loop start -->
        <router-link
          v-for="transaction in transactions"
          :key="transaction.id"
          to="#"
          class="flex items-center p-4 px-5 rounded-lg qr-gray-bg mb-4"
        >
          <img src="@/assets/icons/money.svg" class="list-icon mr-4" />
          <div class>
            <span
              class="
                text-sm text-white
                tracking-wide
                truncate
                w-32
                inline-block
              "
              >{{ transaction.business.business_name | masker }}</span
            >
            <span class="block text-xs text-gray-500 tracking-wide">{{
              transaction.created_at | formatDate
            }}</span>
          </div>
          <div class="ml-auto">
            <span
              :class="{
                'font-semibold tracking-wider text-sm': true,
                'text-white': true,
                'text-green-500': false,
              }"
              >-{{ transaction.transaction_amount }}
              <i class="text-xs">Ksh</i></span
            >
          </div>
        </router-link>

        <!-- Loop end -->
      </div>

      <div
        class="
          flex
          justify-center
          items-center
          fixed
          left-0
          bottom-0
          p-5
          w-full
        "
      >
        <button
          type="button"
          class="
            tracking-wide
            w-full
            p-3
            px-5
            rounded-lg
            h-16
            text-xl
            placeholder-black
            focus:border-teal-500
            focus:outline-none
            tracking-wide
            text-white
          "
        >
          SCAN QR
        </button>
      </div>
    </div>
    <!-- User Customer End-->

    <!-- User Merchant Start -->
    <div
      v-if="currentUser.role == 'merchant' || currentUser.role == 'admin'"
      :class="{
        'relative min-h-screen will-blur': true,
        blur: showMerchantBusinesses || showAddNewBusinessForm,
      }"
    >
      <div class="flex justify-center p-5 w-full">
        <router-link to="#" class="mr-auto">
          <img src="@/assets/icons/menu-user.svg" class="menu-icon" />
        </router-link>
        <span class="font-semibold text-lg text-white tracking-wide capitalize">
          <button
            v-if="false"
            @click="showAddNewBusinessForm = true"
            class="flex items-center"
          >
            <span>Add Business</span>
            <img src="@/assets/icons/plus.svg" class="icon-after-span" />
          </button>
          <button @click="showBusinessesDialog()" class="flex items-center">
            <span>{{ name }}</span>
            <!-- <img src="@/assets/icons/angle-arrow-down.svg" class="icon-after-span" /> -->
          </button>
        </span>
        <button @click.prevent="logout" class="ml-auto">
          <img src="@/assets/icons/power.svg" class="menu-icon" />
        </button>
      </div>

      <div class="flex items-center justify-center flex-col p-5 mt-4">
        <div
          class="
            uppercase
            text-gray-400
            tracking-wide
            text-base
            tracking-wider
            mb-2
          "
        >
          Highest Score
        </div>

        <div class="text-white mb-8 text-2xl font-bold tracking-wider">
          {{ score }}
        </div>

        <div
          class="
            flex
            justify-between
            items-center
            w-full
            rounded-full
            qr-gray-bg
            py-2
            px-6
          "
          v-if="false"
        >
          <button
            class="
              uppercase
              text-gray-400
              tracking-wide
              text-sm
              tracking-wider
              text-center
            "
          >
            Withdraw
          </button>

          <button
            @click="setUpShowEditForm"
            class="
              uppercase
              text-gray-400
              tracking-wide
              text-sm
              tracking-wider
              text-center
              hidden
            "
          >
            Edit
          </button>

          <button
            @click="goTo()"
            class="
              uppercase
              text-gray-400
              tracking-wide
              text-sm
              tracking-wider
              text-center
            "
          >
            QR
          </button>
        </div>

        <a
          :href="`https://codemoran.herokuapp.com/poolpesa/game.html?user=${userId}`"
          class="
            uppercase
            rounded-full
            py-2
            px-6
            shadow-sm
            text-white
            tracking-wide
            text-base
            tracking-wider
            qr-pink-bg
          "
          >Play Now</a
        >
      </div>

      <div
        v-if="transactions.length >= 1"
        class="mt-2 p-5 flex justify-between items-center text-base"
      >
        <span class="text-white font--bold">
          Leaderboard
          <span class="text-gray-400 font-normal hidden">Transactions</span>
        </span>
        <button
          @click="businessTrx(1)"
          class="text-gray-400 text-white inline-flex items-center"
        >
          <span>Reload</span>
          <img src="@/assets/icons/refresh.svg" class="icon-after-span" />
        </button>
      </div>

      <div class="p-5 pb-24">
        <!-- Loop start -->
        <router-link
          v-for="transaction in transactions"
          :key="transaction.id"
          to="#"
          class="flex items-center p-4 px-5 rounded-lg qr-gray-bg mb-4"
        >
          <img src="@/assets/icons/money.svg" class="list-icon mr-4" />
          <div class>
            <span
              class="
                text-sm text-white
                tracking-wide
                truncate
                w-32
                inline-block
              "
              >{{ transaction.user.name }}</span
            >
            <span class="text-sm text-white tracking-wide truncate block">{{
              transaction.user.phone | masker
            }}</span>
            <span class="block text-xs text-gray-500 tracking-wide mt-2">{{
              transaction.updated_at | formatDate
            }}</span>
          </div>
          <div class="ml-auto">
            <span
              :class="{
                'font-semibold tracking-wider text-sm': true,
                'text-white': true,
                'text-green-500': false,
              }"
              >{{ transaction.score }}</span
            >
          </div>
        </router-link>

        <!-- Loop end -->
      </div>
    </div>
    <!-- User Merchant End-->

    <!-- Merchant Business List -->
    <transition name="popup">
      <div
        v-if="showMerchantBusinesses"
        class="
          absolute
          top-0
          left-0
          p-5
          h-screen
          oveflowy-auto
          w-full
          overflow-y-auto
        "
      >
        <div class="p-1 pb-16">
          <div class="pb-5 flex justify-between">
            <button
              @click="
                showMerchantBusinesses = false;
                lockAppScroll = false;
              "
            >
              <img src="@/assets/icons/close-white.svg" class="w-6" />
            </button>
          </div>

          <div
            class="flex items-center p-4 px-5 rounded-lg qr-gray-bg mb-4"
            v-for="business in businesses"
            :key="business.id"
          >
            <router-link to="#" class="inline-flex items-center">
              <img src="@/assets/icons/shop.svg" class="list-icon mr-4" />
              <div class>
                <span
                  class="
                    text-sm text-gray-500
                    tracking-wide
                    truncate
                    w-24
                    inline-block
                  "
                  >{{ business.business_name }}</span
                >
                <span class="block text-xs text-gray-500 tracking-wide">{{
                  business.business_location
                }}</span>
              </div>
            </router-link>
            <div class="ml-auto">
              <button
                @click="changeActiveBusiness(business)"
                class="
                  font-semibold
                  tracking-wider
                  text-xs
                  rounded-full
                  text-white
                  qr-btn
                  py-1
                  px-3
                  flex
                  justify-center
                  items-center
                "
              >
                Select
              </button>
            </div>
          </div>

          <!-- Add New Business Button -->
          <div
            class="
              fixed
              bottom-0
              left-0
              pb-4
              flex
              justify-center
              items-center
              w-full
            "
          >
            <button
              @click="showAddNewBusinessForm = true"
              class="
                flex
                items-center
                font-semibold
                tracking-wider
                text-xs
                rounded-full
                text-white
                qr-btn
                py-2
                px-4
                flex
                justify-center
                items-center
              "
            >
              <span>Add Business</span>
              <img src="@/assets/icons/plus.svg" class="icon-after-span" />
            </button>
          </div>
        </div>
      </div>
    </transition>
    <!--  Merchant Business List End -->

    <!-- Add New Business -->
    <transition name="popup">
      <div
        v-if="showAddNewBusinessForm"
        class="absolute top-0 left-0 w-full p-5"
      >
        <form class="bg-white rounded-lg p-5" @submit.prevent="addNewBusiness">
          <div>
            <button type="button" @click="showAddNewBusinessForm = false">
              <img src="@/assets/icons/close.svg" class="w-4" />
            </button>
          </div>

          <div
            class="text-center text-xl flex items-center justify-center w-full"
          >
            <span
              class="
                text-xm
                rounded-full
                qr-pink-bg
                w-5
                h-5
                flex
                justify-center
                items-center
                text-white
                font-light
                mr-2
              "
              >+</span
            >
            <span class="font-bold mr-2">Add</span> Business
          </div>

          <div class="py-10">
            <div class="mb-5">
              <label for="new_business_name" class="mb-1 inline-block"
                >Business Name</label
              >
              <input
                v-model="addBusinessForm.business_name"
                type="text"
                placeholder="e.g Jayden Mini Store"
                id="new_business_name"
                required
                autocomplete="off"
                class="
                  w-full
                  p-3
                  px-5
                  border-2 border-opacity-50 border-pink-700
                  rounded-lg
                  h-12
                  text-md
                  placeholder-black placeholder-opacity-50
                  focus:border-teal-500
                  focus:outline-none
                  tracking-wide
                "
              />
            </div>
            <div class="mb-8">
              <label for="email" class="mb-1 inline-block"
                >Business Location</label
              >
              <input
                v-model="addBusinessForm.business_location"
                type="text"
                placeholder="e.g Muthaiga Estate"
                id="new_business_location"
                required
                autocomplete="off"
                class="
                  w-full
                  p-3
                  px-5
                  border-2 border-opacity-50 border-pink-700
                  rounded-lg
                  h-12
                  text-md
                  placeholder-black placeholder-opacity-50
                  focus:border-teal-500
                  focus:outline-none
                  tracking-wide
                "
              />
            </div>
            <div class="mb-5 flex justify-center">
              <button
                class="
                  w-full
                  p-3
                  px-5
                  border border-pink-700
                  rounded-full
                  shadow-md
                  h-14
                  font-semibold
                  text-md
                  placeholder-black
                  focus:border-teal-500
                  focus:outline-none
                  tracking-wider
                  qr-btn
                  text-white
                "
              >
                ADD BUSINESS
              </button>
            </div>
          </div>
        </form>
      </div>
    </transition>
    <!-- Add New Business End -->

    <!-- Edit Business -->
    <transition name="popup">
      <div v-if="showEditBusinessForm" class="absolute top-0 left-0 w-full p-5">
        <form class="bg-white rounded-lg p-5" @submit.prevent="editBusiness">
          <div>
            <button type="button" @click="showEditBusinessForm = false">
              <img src="@/assets/icons/close.svg" class="w-4" />
            </button>
          </div>

          <div
            class="text-center text-xl flex items-center justify-center w-full"
          >
            <span
              class="
                text-xm
                rounded-full
                qr-pink-bg
                w-5
                h-5
                flex
                justify-center
                items-center
                text-white
                font-light
                mr-2
              "
              >+</span
            >
            <span class="font-bold mr-2">Edit</span> Business
          </div>

          <div class="py-10">
            <div class="mb-5">
              <label for="new_business_name" class="mb-1 inline-block"
                >Business Name</label
              >
              <input
                v-model="editBusinessForm.business_name"
                type="text"
                placeholder="e.g Jayden Mini Store"
                id="new_business_name"
                required
                autocomplete="off"
                class="
                  w-full
                  p-3
                  px-5
                  border-2 border-opacity-50 border-pink-700
                  rounded-lg
                  h-12
                  text-md
                  placeholder-black placeholder-opacity-50
                  focus:border-teal-500
                  focus:outline-none
                  tracking-wide
                "
              />
            </div>
            <div class="mb-8">
              <label for="email" class="mb-1 inline-block"
                >Business Location</label
              >
              <input
                v-model="editBusinessForm.business_location"
                type="text"
                placeholder="e.g Muthaiga Estate"
                id="new_business_location"
                required
                autocomplete="off"
                class="
                  w-full
                  p-3
                  px-5
                  border-2 border-opacity-50 border-pink-700
                  rounded-lg
                  h-12
                  text-md
                  placeholder-black placeholder-opacity-50
                  focus:border-teal-500
                  focus:outline-none
                  tracking-wide
                "
              />
            </div>
            <div class="mb-5 flex justify-center">
              <button
                class="
                  w-full
                  p-3
                  px-5
                  border border-pink-700
                  rounded-full
                  shadow-md
                  h-14
                  font-semibold
                  text-md
                  placeholder-black
                  focus:border-teal-500
                  focus:outline-none
                  tracking-wider
                  qr-btn
                  text-white
                "
              >
                SAVE CHANGES
              </button>
            </div>
          </div>
        </form>
      </div>
    </transition>
    <!-- Edit Business End -->

    <!-- Floating Toolbar-->
    <div
      class="toolbar fixed bottom-0 left-0 w-full p-5"
      v-show="showToolbar == true && currentUser.role != 'customer'"
    >
      <div class="shadow-lg mx-auto rounded-md flex justify-between qr-gray-bg">
        <router-link
          to="#"
          :class="{
            'p-3 flex-1 flex justify-center': true,
            'qr-pink-bg': currentView == 'home',
          }"
        >
          <img
            src="@/assets/img/home.png"
            alt
            class="w-6"
            width="24px"
            height="24px"
          />
        </router-link>

        <!-- <button @click.prevent="scanQR()" :class="{'p-3 flex-1 flex justify-center':true}">
          <img src="@/assets/img/qr-code.png" alt class="w-6" width="24px" height="24px" />
        </button> -->

        <router-link to="#" :class="{ 'p-3 flex-1 flex justify-center': true }">
          <img
            src="@/assets/img/menu-user.png"
            alt
            class="w-6"
            width="24px"
            height="24px"
          />
        </router-link>
      </div>
    </div>
    <!-- Floating toolbar end -->
  </div>
</template>

<script>
import {
  createBusiness,
  updateBusiness,
  getUserBusinesses,
  getCustomerTrx,
  getBusinessTrx,
} from "@/api/business";
import { mapGetters } from "vuex";
import { getToken } from "@/utils/auth"; // get token from cookie
import { QrcodeStream } from 'vue-qrcode-reader'

export default {
  name: "Home",
  data() {
    return {
      token: null,
      currentUser: { role: null },
      hasBusiness: false,
      showAddNewBusinessForm: false,
      showEditBusinessForm: false,
      showMerchantBusinesses: false,
      loading: "",
      lockAppScroll: false,
      currentView: "home",
      showToolbar: true,
      transactions: [],
      addBusinessFormDefault: {
        business_name: null,
        business_location: null,
        business_photo: null,
        user_id: 0,
        is_active: true,
      },
      addBusinessForm: {
        business_name: null,
        business_location: null,
        business_photo: null,
        user_id: 0,
        is_active: true,
      },
      editBusinessForm: {
        id: null,
        business_name: null,
        business_location: null,
        business_photo: null,
        user_id: 0,
        is_active: true,
      },
      businesses: [],
      activeBusiness: {
        id: null,
        business_name: null,
        business_location: null,
        business_photo: null,
        business_transaction: 0.0,
      },
    };
  },
  components: {
    QrcodeStream
  },
  computed: {
    ...mapGetters(["name", "userId", "role", "score"]),
  },
  created() {
    this.currentUser.role = this.role;
    this.token = getToken();
  },
  mounted() {
    let self = this;
    if (self.role == "merchant" || self.role == "admin") {
      self.businessTrx(1);
    } else {
      self.customerTrx();
    }
  },
  watch: {
    currentUser: function () {
      if (this.currentUser.role == "customer") {
        this.showToolbar = false;
      }
    },
    showAddNewBusinessForm: function () {
      if (this.showAddNewBusinessForm) {
        this.showToolbar = false;
      } else {
        if (this.showMerchantBusinesses) {
          this.showToolbar = false;
        } else {
          this.showToolbar = true;
        }
      }
    },
    showEditBusinessForm: function () {
      if (this.showEditBusinessForm) {
        this.showToolbar = false;
      } else {
        if (this.showMerchantBusinesses) {
          this.showToolbar = false;
        } else {
          this.showToolbar = true;
        }
      }
    },
    showMerchantBusinesses: function () {
      if (this.showMerchantBusinesses) {
        this.showToolbar = false;
      } else {
        this.showToolbar = true;
      }
    },
  },
  methods: {
    async logout() {
      await this.$store.dispatch("user/logout");
      this.$router.push(`/login?redirect=${this.$route.fullPath}`);
    },
    //Customer Methods
    scanQR(decodedString) {
      let self = this;

      self.$router.push({ path: `/payment/${decodedString}` });
      return;

      /* eslint-disable */
      cordova.plugins.barcodeScanner.scan(
        function (result) {
          if (result !== null || result !== "") {
            //redirect to payment
            self.$router.push({ path: `/payment/${result.text}` });
          }
        },
        function (error) {
          let text = `Scanning failed: ${error}`;
          self.$msg.error({
            message: text,
            width: "80%",
            position: "center",
          });
        },
        {
          preferFrontCamera: false,
          showFlipCameraButton: true,
          showTorchButton: true,
          saveHistory: true,
          prompt: "Place the QR Code inside the scan area", // Android
          resultDisplayDuration: 500,
          orientation: "portrait",
        }
      );
    },
    //Merchant Methods
    showBusinessesDialog() {
      let self = this;
      self.lockAppScroll = true;
      self.showMerchantBusinesses = true;
    },
    getBusinesses() {
      let self = this;
      let loading = self.$loading.show({
        color: "purple",
        canCancel: false,
      });
      getUserBusinesses(this.token, this.userId)
        .then(
          (response) => {
            self.businesses = response.business;
            if (self.businesses.length >= 1) {
              self.hasBusiness = true;
              //TO DO; Return total business transactions
              self.setActiveBusiness(self.businesses[0], 245000);
              loading.hide();
            } else {
              loading.hide();
            }
          },
          (error) => {
            loading.hide();
            self.$msg.error({
              message: `Request failed! ${error}`,
              width: "80%",
              position: "bottom-center",
            });
          }
        )
        .catch((err) => {
          loading.hide();
          self.$msg.error({
            message: `Connection! ${err}`,
            width: "80%",
            position: "bottom-center",
          });
        });
    },
    setActiveBusiness(business, transactions) {
      let self = this;
      self.activeBusiness.id = business.id;
      self.activeBusiness.business_name = business.business_name;
      self.activeBusiness.business_location = business.business_location;
      //self.activeBusiness.business_transaction = transactions;
      self.businessTrx(business.id);
    },
    addNewBusiness() {
      let self = this;
      if (
        self.addBusinessForm.business_name == "" ||
        self.addBusinessForm.business_location == ""
      ) {
        return;
      }

      let loading = self.$loading.show({
        color: "purple",
        canCancel: false,
      });

      this.addBusinessForm.user_id = this.userId;

      createBusiness(this.token, self.addBusinessForm)
        .then(
          (response) => {
            loading.hide();
            self.getBusinesses();
            self.addBusinessForm = self.addBusinessFormDefault;
            self.showAddNewBusinessForm = false;
            self.$msg.success({
              message: `${response.message}`,
              width: "80%",
              position: "bottom-center",
            });
          },
          (error) => {
            loading.hide();
            self.$msg.error({
              message: `Request failed! ${error}`,
              width: "80%",
              position: "bottom-center",
            });
          }
        )
        .catch((err) => {
          loading.hide();
          self.$msg.error({
            message: `Request up failed! ${err}`,
            width: "80%",
            position: "bottom-center",
          });
        });
    },
    editBusiness() {
      let self = this;
      if (
        self.editBusinessForm.business_name == "" ||
        self.editBusinessForm.business_location == ""
      ) {
        return;
      }

      let loading = self.$loading.show({
        color: "purple",
        canCancel: false,
      });

      updateBusiness(
        self.token,
        self.editBusinessForm.id,
        self.editBusinessForm
      )
        .then(
          (response) => {
            loading.hide();
            console.log(response);
            self.getBusinesses();
            self.editBusinessForm = {};
            self.showEditBusinessForm = false;
            self.$msg.success({
              message: `${response.message}`,
              width: "80%",
              position: "bottom-center",
            });
          },
          (error) => {
            loading.hide();
            self.$msg.error({
              message: `Request failed! ${error}`,
              width: "80%",
              position: "bottom-center",
            });
          }
        )
        .catch((err) => {
          loading.hide();
          self.$msg.error({
            message: `Request up failed! ${err}`,
            width: "80%",
            position: "bottom-center",
          });
        });
    },
    getTransactions() {
      console.log("Fetch transactions");
    },
    changeActiveBusiness(business) {
      let self = this;
      let trx = self.getRandomTransaction();
      self.setActiveBusiness(business, trx);
      (self.lockAppScroll = false), (self.showMerchantBusinesses = false);
    },
    getRandomTransaction() {
      let min = 14499;
      let max = 98655;
      return (Math.random() * (max - min) + min).toFixed(2);
    },
    setUpShowEditForm() {
      let self = this;
      self.editBusinessForm.id = self.activeBusiness.id;
      self.editBusinessForm.business_name = self.activeBusiness.business_name;
      self.editBusinessForm.business_location =
        self.activeBusiness.business_location;
      self.editBusinessForm.business_photo = self.activeBusiness.business_photo;
      self.editBusinessForm.user_id = self.userId;
      self.editBusinessForm.is_active = true;

      //show popup
      self.showEditBusinessForm = true;
    },
    goTo() {
      let self = this;
      let path = `/qr/${self.activeBusiness.id}/${self.activeBusiness.business_name}`;
      this.$router.push({ path });
    },
    customerTrx() {
      let self = this;
      let loading = self.$loading.show({
        color: "purple",
        canCancel: false,
      });
      getCustomerTrx(this.token, this.userId)
        .then(
          (response) => {
            let trx = response.transactions;
            if (trx.data.length >= 1) {
              console.log("Customer Trx Total", trx.total);
              console.log("Customer Trx Data", trx.data);
              self.transactions = trx.data;
              loading.hide();
            } else {
              console.log("No Customer Trx", trx);
              loading.hide();
            }
          },
          (error) => {
            console.log(`Error ${error}`);
            loading.hide();
          }
        )
        .catch((err) => {
          console.log(`Error ${err}`);
          loading.hide();
        });
    },
    businessTrx(businessId) {
      let self = this;
      let loading = self.$loading.show({
        color: "purple",
        canCancel: false,
      });
      getBusinessTrx(this.token, businessId)
        .then(
          (response) => {
            let trx = response;
            if (trx.length >= 1) {
              self.transactions = trx;
              //self.activeBusiness.business_transaction = trx.total;
              loading.hide();
            } else {
              console.log("No Business Trx");
              loading.hide();
            }
          },
          (error) => {
            console.log(`Error ${error}`);
            loading.hide();
          }
        )
        .catch((err) => {
          console.log(`Error ${err}`);
          loading.hide();
        });
    },
  },
};
</script>

