<template>
  <div :class="{ 'app-container relative': true }">
    <!-- User Merchant Start -->
    <div :class="{ 'relative min-h-screen will-blur': true }">
      <div class="flex justify-center p-5 w-full">
        <router-link to="/" class="mr-auto">
          <img src="@/assets/icons/arrow.svg" class="menu-icon" />
        </router-link>
        <span class="font-semibold text-lg text-white tracking-wide capitalize">
          <button class="flex items-center">
            <span>Scan Results</span>
            <img src="@/assets/icons/tick.svg" class="icon-after-span" />
          </button>
        </span>
        <router-link to="#" class="ml-auto">&nbsp;</router-link>
      </div>

      <div class="flex items-center justify-center flex-col p-5 mt-4 min-h-screen">
        <!-- <div
          v-if="validBusiness"
          class="
            uppercase
            text-gray-400
            tracking-wide
            text-base
            tracking-wider
            mb-2
          "
        >
          Make Payment to
        </div> -->

          <div
            v-if="validBusiness"
            class="
              text-white
              mb-8
              text-3xl
              font-bold
              tracking-wider
              text-center
            "
          >
            {{ this.businessId }}
          </div>

          
        <div v-if="false">

          <div
            v-if="loadingBusiness"
            class="
              text-white
              mb-8
              text-2xl
              mt-8
              font-light
              uppercase
              opacity-50
              tracking-wider
              text-center
            "
          >
            Loading...
          </div>

          <div
            v-if="notValidBusiness"
            class="
              text-white
              mb-8
              text-2xl
              font-bold
              tracking-wider
              text-center
            "
          >
            Could not verify the QR Code, try again!
          </div>

          <router-link
            v-if="notValidBusiness"
            to="/"
            class="
              uppercase
              rounded-full
              py-4
              px-6
              text-gray-400
              tracking-wide
              text-base
              tracking-wider
              qr-gray-bg
            "
            >Try Again</router-link
          >

          <form
            class="w-full py-4"
            v-if="validBusiness"
            @submit.prevent="requestPushSTK"
          >
            <div class="mb-8">
              <label for="email" class="mb-1 inline-block text-white"
                >Preferrd Number for Payment</label
              >
              <input
                type="tel"
                placeholder="Enter "
                id="amount"
                min="2"
                v-model="paymentPhone"
                required
                autocomplete="off"
                class="
                  w-full
                  p-3
                  px-5
                  tracking-wider
                  border-2 border-opacity-50
                  rounded-lg
                  h-12
                  text-lg
                  placeholder-black placeholder-opacity-50
                  focus:border-teal-500
                  focus:outline-none
                  tracking-wide
                "
              />
            </div>
            <div class="mb-8">
              <label for="email" class="mb-1 inline-block text-white"
                >Enter Amount</label
              >
              <input
                type="number"
                placeholder="Enter amount"
                id="amount"
                min="2"
                v-model="paymentAmount"
                required
                autocomplete="off"
                class="
                  w-full
                  p-3
                  px-5
                  tracking-wider
                  border-2 border-opacity-50
                  rounded-lg
                  h-12
                  text-lg
                  placeholder-black placeholder-opacity-50
                  focus:border-teal-500
                  focus:outline-none
                  tracking-wide
                "
              />
            </div>
            <button
              class="
                tracking-wide
                w-full
                py-4
                px-5
                mt-4
                border border-pink-700
                rounded-lg
                h-16
                text-xl
                placeholder-black
                focus:border-teal-500
                focus:outline-none
                tracking-wide
                qr-btn
                text-white
              "
            >
              SCAN
            </button>
          </form>
        </div>
      </div>
    </div>
    <!-- User Merchant End-->
    <div
      v-if="showFeedback"
      class="
        fixed
        absolute
        w-full
        flex
        items-center
        justify-center
        h-full
        overflow-hidden
        top-0
        left-0
        bg-black bg-opacity-75
      "
    >
      <div class="p-6 bg-gray-700 rounded-sm">
        <p class="text-xl text-white">{{ feedbackMessage }}</p>
        <button
          @click="afterFeedback"
          class="
            tracking-wide
            w-full
            py-2
            px-4
            mt-4
            border border-pink-700
            rounded-lg
            text-xl
            placeholder-black
            focus:border-teal-500
            focus:outline-none
            tracking-wide
            qr-btn
            text-white
          "
        >
          OK
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { getBusiness, triggerSTK } from "@/api/business";
import { mapGetters } from "vuex";
import { getToken } from "@/utils/auth"; // get token from cookie

export default {
  name: "Payments",
  data() {
    return {
      token: null,
      currentUser: { role: null },
      businessId: "",
      loading: "",
      validBusiness: false,
      notValidBusiness: false,
      loadingBusiness: true,
      paymentPhone: null,
      paymentAmount: "",
      business: {},
      feedbackMessage: "",
      showFeedback: false,
      feedbackCode: null,
    };
  },
  computed: {
    ...mapGetters(["name", "userId", "phone"]),
  },
  created() {
    this.currentUser.role = "merchant";
    this.token = getToken();
    this.paymentPhone = this.phone;
    this.businessId = this.$route.params.id;
  },
  mounted() {
    let self = this;
    self.getABusiness();
  },
  watch: {},
  methods: {
    async logout() {
      await this.$store.dispatch("user/logout");
      this.$router.push(`/login?redirect=${this.$route.fullPath}`);
    },
    getABusiness() {
      let self = this;
      let loading = self.$loading.show({
        color: "purple",
        canCancel: false,
      });
      getBusiness(this.token, 1)
        .then(
          (response) => {
            self.business = response.business;
            self.validBusiness = true;
            (self.loadingBusiness = false), console.log(response);
            loading.hide();
          },
          (error) => {
            loading.hide();
            self.validBusiness = false;
            self.notValidBusiness = true;
            (self.loadingBusiness = false),
              self.$msg.error({
                message: `Request failed! ${error}`,
                width: "80%",
                position: "bottom-center",
              });
          }
        )
        .catch((err) => {
          loading.hide();
          self.validBusiness = false;
          self.notValidBusiness = true;
          (self.loadingBusiness = false),
            self.$msg.error({
              message: `Request up failed! ${err}`,
              width: "80%",
              position: "bottom-center",
            });
        });
    },
    requestPushSTK() {
      let self = this;

      if (self.paymentAmount <= 0) {
        return;
      }

      if (self.paymentPhone == "") {
        return;
      }

      let paymentRefData = {
        businessId: self.businessId,
        userId: self.userId,
        userName: self.name,
        phone: self.paymentPhone,
        amount: self.paymentAmount,
      };

      let loading = self.$loading.show({
        color: "purple",
        canCancel: false,
      });

      triggerSTK(self.token, paymentRefData)
        .then(
          (response) => {
            console.log("Data", response);
            let res = response.data;
            self.showFeedback = true;
            self.feedbackMessage = res.message;
            self.feedbackCode = res.code;
            loading.hide();
          },
          (error) => {
            loading.hide();
            self.$msg.error({
              message: `Request failed! ${error}`,
              width: "80%",
              position: "bottom-center",
            });
          }
        )
        .catch((err) => {
          loading.hide();
          self.$msg.error({
            message: `Request failed! ${err}`,
            width: "80%",
            position: "bottom-center",
          });
        });
    },
    afterFeedback() {
      console.log(this.feedbackCode);
      if (this.feedbackCode !== "success") {
        this.showFeedback = false;
      } else {
        let path = `/`;
        this.$router.push({ path });
      }
    },
    goTo() {
      let self = this;
      let path = `/qr/${self.activeBusiness.id}/${self.activeBusiness.business_name}`;
      this.$router.push({ path });
    },
  },
};
</script>

<style lang="scss" scoped>
.app-container {
  background: #3c3d4f;
}

.menu-icon {
  width: 30px;
}

.qr-image {
  max-height: 200px;
}

.qr-pink-bg {
  background: rgb(156, 56, 216);
  background: linear-gradient(
    90deg,
    rgba(156, 56, 216, 1) 0%,
    rgba(252, 110, 152, 1) 100%
  );
}

.qr-btn {
  background: rgb(156, 56, 216);
  background: linear-gradient(
    90deg,
    rgba(156, 56, 216, 1) 0%,
    rgba(252, 110, 152, 1) 100%
  );
}

.qr-gray-bg {
  background: #2e313a;
  background: linear-gradient(90deg, #2e313a 0%, #474956 100%);
}

.qr-borders {
  &::after,
  &::before {
    content: "";
    position: absolute;
    background: #999;
  }
}

.qr-top-left {
  &:before {
    top: 0;
    left: 0;
    width: 25%;
    height: 4px;
  }
  &:after {
    top: 0;
    left: 0;
    width: 4px;
    height: 25%;
  }
}

.qr-bottom-right {
  &:before {
    bottom: 0;
    right: 0;
    width: 25%;
    height: 4px;
  }
  &:after {
    bottom: 0;
    right: 0;
    width: 4px;
    height: 25%;
  }
}

.icon-after-span {
  width: 15px;
  margin-left: 0.5rem;
}

.list-icon {
  width: 35px;
}

.will-blur {
  transition: filter 0.5s ease-out;
}

.blur {
  pointer-events: none;
  filter: blur(3px) contrast(0.4);
}

/* Popup Animations */
.popup-enter,
.popup-leave-to {
  opacity: 0;
}

.popup-enter-to,
.popup-leave {
  opacity: 1;
}

.popup-enter-active,
.popup-leave-active {
  transition: all 0.3s ease-out;
}
</style>

